.button-blue {
  width: 90%;
  margin: auto;
}

.login-message {
  font-family: Arial, sans-serif;
  font-size: 16px;
  margin: 10px;
}

.login-link {
  color: #3498db;
  text-decoration: underline;
  cursor: pointer;
}

.login-link:hover {
  color: #2980b9;
}

.login-text {
  font-weight: bold;
  margin-right: 5px;
}